.text-field {
  position: relative;
}
.text-field--required::before {
  content: "*";
  position: absolute;
  top: 5px;
  left: 15px;
  color: #fe7865;
}

.text-field label {
  position: absolute;
  top: 16px;
  left: 15px;
  width: calc(100% - 40px);
  overflow: hidden;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: normal;
  white-space: nowrap;
  text-overflow: ellipsis;
  cursor: text;
  user-select: none;
}

.text-field--required label {
  left: 25px;
}

.text-field--number label::before {
  content: "+7   ";
  font-size: 14px;
  line-height: 17px;
}

.text-field input {
  box-sizing: border-box;
  width: 100%;
  height: 50px;
  padding-right: 15px;
  padding-left: 15px;
  font-weight: 400;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  font-family: inherit;
  border: 1px solid rgba(44, 46, 63, 0.3);
}
.text-field input::-webkit-input-placeholder {
  opacity: 0;
}
.text-field input::-moz-placeholder {
  opacity: 0;
}
.text-field input:-moz-placeholder {
  opacity: 0;
}
.text-field input:-ms-input-placeholder {
  opacity: 0;
}
.text-field input::placeholder {
  opacity: 0;
}

.text-field--required input {
  padding-left: 24px;
}

.text-field--number input {
  padding-left: 24px;
}

.text-field input:focus ~ label,
.text-field input:not(:placeholder-shown) ~ label {
  visibility: hidden;
}

